/* menu start */
.hmhd12 .hmhd12-16 {
  border-top:1px #ECD5BA solid;
}

.hmhd12-16 nav {
  position: relative;
  z-index: 99;
}

.hmhd12-16 nav ul {
  display: flex;
  padding: 0px;
  margin: 0px;
  list-style: none;
  position: relative;
  justify-content: space-between;
  flex-wrap: wrap;
	}

.hmhd12-16 nav ul li {
	display:inline-block;
	}

.hmhd12-16 nav a {
  display: block;
  padding: 0px 10px;
  color: #C05F30;
  font-size: 14px;
  line-height: 60px;
  text-decoration: none;
}

.hmhd12-16 nav a:hover {
  background-color: rgb(252 245 238);
}

/* Hide Dropdowns by Default */
.hmhd12-16 nav ul ul {
	display: none;
	position: absolute;
	top: 60px; /* the height of the main nav */
}

/* Display Dropdowns on Hover */
.hmhd12-16 nav ul li:hover > ul {
	display:inherit;
}

/* Fisrt Tier Dropdown */
.hmhd12-16 nav ul ul li {
  width: 170px;
  float: none;
  display: list-item;
  position: relative;
  background: #c05f30;
}
.hmhd12-16 nav ul ul li a{
  line-height: 20px;
  color: #fff;
  padding: 10px 10px 10px;
  border-bottom: 1px #c3a887 solid;
}
.hmhd12-16 nav ul ul li a:hover{
  background: rgb(213 120 76);
}

/* Second, Third and more Tiers	*/
.hmhd12-16 nav ul ul ul li {
	position: relative;
	top:-60px;
	left:170px;
}


/* Change this in order to change the Dropdown symbol */
.hmhd12-16 li > a:after {
  content:  ' +';
  display: none;
}
.hmhd12-16 li > a:only-child:after {
  content: '';
  display: none;
}

/* menu end */


